import { Location } from '@angular/common';
import { Component, Input } from '@angular/core';
import { Router } from '@angular/router';
import { TooltipComponent } from '@front-shared-lib';

@Component({
  selector: 'app-admin-back-button',
  standalone: true,
  imports: [
    TooltipComponent
  ],
  templateUrl: './admin-back-button.component.html',
  styleUrl: './admin-back-button.component.scss'
})
export class AdminBackButtonComponent {
  @Input() backRoute?: string;

  constructor(
    private router: Router,
    private locaton: Location
  ) {}

  goBack() {
    if (this.backRoute) {
      this.router.navigate([this.backRoute]);
    } else {
      this.locaton.back();
    }
  }
}
